import React, { Component } from "react";
import PricingCard from "./PricingCard.js";


class PricingLayout extends Component {
  render() {
    return (
        <section className="container p-2 mt-2 mb-2" id="pricing">
            <h2 className="display-2" style={{color: "#e14eca"}}> Our Pricing Charts </h2>
            <div className="container row">
                <div className="col-sm-12 col-md-4 col-lg-4">
                    <PricingCard
                        gradientFirst="#fc00ff"
                        gradientSecond="#00dbde"
                        icon="icofont-paper-plane"
                        planTitle="Hobbyist"
                        planCost="35"
                        features={["10 Pseudo Users", "200 Files Monitoring", "Unlimited Usage"]}
                    />
                </div>
                <div className="col-sm-12 col-md-4 col-lg-4">
                    <PricingCard
                        gradientFirst="#FF5F6D"
                        gradientSecond="#FFC371"
                        icon="icofont-airplane-alt"
                        planTitle="Professional"
                        planCost="80"
                        features={["25 Pseudo Users", "1000 Files Monitoring", "Unlimited Usage"]}
                    />
                </div>
                <div className="col-sm-12 col-md-4 col-lg-4">
                    <PricingCard
                        gradientFirst="#24C6DC"
                        gradientSecond="#514A9D"
                        icon="icofont-rocket"
                        planTitle="Elite"
                        planCost="100"
                        features={["Unlimited Pseudo Users", "2500 Files Monitoring", "Unlimited Usage"]}
                    />
                </div>
            </div>
        </section>
    );
  }
}

export default PricingLayout;