import React, {useState, useEffect, useRef} from "react";
import classNames from "classnames";
import {
  Collapse,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  NavbarBrand,
  Navbar,
  NavLink,
  Nav,
  Container
} from "reactstrap";
import { connect } from 'react-redux';
import {  Redirect } from 'react-router-dom';
import {logout} from '../../actions/auth';
const AdminNavbar = ({brandText,
  toggleSidebar,
  sidebarOpened, isAuthenticated, logout}) =>{
  const [collapseOpen, setCollapseOpen] = useState(false);
  const [color, setColor] = useState('navbar-transparent');

  const logout_user = () => {
    logout();
  };

  const updateColor = () =>{
    if(window.innerWidth<993 && collapseOpen){
      setColor("bg-white");
    }else{
      setColor("navbar-transparent");
    }
  };
  // componentDidMount
  useEffect(()=>{
    window.addEventListener('resize', updateColor);
  },[])
  
  // componentWillUnmount
  useEffect(() => {
    
    return () => {
      window.removeEventListener("resize", updateColor);
    }
  }, []);

  const toggleCollapse = () => {
    if (collapseOpen) {
      setColor("navbar-transparent");
    } else {
      setColor("bg-white");
    }
    setCollapseOpen(!collapseOpen);
  };


  return (
    
    <>
    {isAuthenticated ? '':<Redirect to="/common/home"/>}
      <Navbar
        className={classNames("navbar-absolute", color)}
        expand="lg"
      >
        <Container fluid>
          <div className="navbar-wrapper">
            <div
              className={classNames("navbar-toggle d-inline", {
                toggled: sidebarOpened
              })}
            >
              <button
                className="navbar-toggler"
                style={{backgroundColor:"transparent"}}
                type="button"
                onClick={toggleSidebar}
              >
                <span className="navbar-toggler-bar bar1" />
                <span className="navbar-toggler-bar bar2" />
                <span className="navbar-toggler-bar bar3" />
              </button>
            </div>
            <NavbarBrand href="#pablo" onClick={e => e.preventDefault()}>
              {brandText}
            </NavbarBrand>
          </div>
          <button
            aria-expanded={false}
            aria-label="Toggle navigation"
            className="navbar-toggler"
            style={{backgroundColor:"transparent"}}
            data-target="#navigation"
            data-toggle="collapse"
            id="navigation"
            type="button"
            onClick={toggleCollapse}
          >
            <span className="navbar-toggler-bar navbar-kebab" />
            <span className="navbar-toggler-bar navbar-kebab" />
            <span className="navbar-toggler-bar navbar-kebab" />
          </button>
          <Collapse navbar isOpen={collapseOpen}>
            <Nav className="ml-auto" navbar>
              <UncontrolledDropdown nav>
                <DropdownToggle
                  caret
                  color="default"
                  data-toggle="dropdown"
                  nav
                  onClick={e => e.preventDefault()}
                >
                  <div className="photo">
                    <img alt="..." src={require("assets/img/anime3.png")} />
                  </div>
                  <b className="caret d-none d-lg-block d-xl-block" />
                  <p onClick={logout_user} className="d-lg-none">Log out</p>
                </DropdownToggle>
                <DropdownMenu className="dropdown-navbar" right tag="ul">
                  <NavLink tag="li">
                    <DropdownItem className="nav-item">Profile</DropdownItem>
                  </NavLink>
                  <NavLink tag="li">
                    <DropdownItem className="nav-item">Settings</DropdownItem>
                  </NavLink>
                  <DropdownItem divider tag="li" />
                  <NavLink tag="li">
                    <DropdownItem className="nav-item" onClick={logout_user}>Logout</DropdownItem>
                  </NavLink>
                </DropdownMenu>
              </UncontrolledDropdown>
              <li className="separator d-lg-none" />
            </Nav>
          </Collapse>
        </Container>
      </Navbar>
    </>
  );
}
const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { logout })(AdminNavbar);
